html, body, #app, #root {
    height: 100%;
    margin: 0;
}

body {
    font-family: 'promixa-nova', sans-serif;
    color: black;
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #F5F5F5;
    display:flex;
    flex-direction:column;
}

#search {
    background: url('../images/balrog.jpg') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover !important;
    z-index: 1;
    -webkit-transform: translateZ(0);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#search_container_default {
    background: rgba(31, 31, 31, .35);
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 12em;
    max-width: 50rem;
    vertical-align: middle;
    margin: auto;
    width: 80%;
    z-index: 1;
}

#section {
    padding:1.5rem;
    margin:0 auto;
    position: relative;
    text-align: center;
    vertical-align: middle;
}

.query {
    color: white;
    font-size: 2.5em;
}

.submit {
    position:relative;
    padding:6px 15px;
    left:-8px;
    color:#fafafa;
}

#content {
    background-color: #F5F5F5;
    position: relative;
    margin-top: auto;
    width: 100%;
    z-index: 2;
    color: black;
}

#cards {
    margin: 0 auto;
    max-width: 50em;
    height: 100%;
    overflow: auto;
}

.card {
    background-color: white;
    margin: 0 1rem 15px;
    overflow: hidden;
    padding: 0 15px 0;
}

.book_chap {
    font-size: 1.2em;
    position: relative;
    background-color: white;
    margin-bottom: 15px;
    margin-top: 0;
    padding-top: 15px;
    line-height: 1.125;
}
.book_chap_shadow {
    -webkit-box-shadow: -6px 18px 25px 8px #ffffff;
}

.last_shadow {
    position: absolute;
    bottom: 0;
    height: 15px;
    background: white;
    width: 100%;
    -webkit-box-shadow: -6px -5px 25px 15px #ffffff;
}
.last_shadow_clicked {
    display: none;
}

.paragraphs {
    font-size: 0.8em;
    line-height: 1.5;
}

.results_found {
    text-align: center;
    margin-bottom: 1em;
    margin-top: 1em;
}

.prev_clicked {
    margin-top: 14px;
    -moz-transition: margin-top 0.3s;
    -o-transition: margin-top 0.3s;
    -ms-transition: margin-top 0.3s;
    transition: margin-top 0.3s;
}

.prev {
    margin-top: auto;
    -moz-transition: margin-top 0.3s;
    -o-transition: margin-top 0.3s;
    -ms-transition: margin-top 0.3s;
    transition: margin-top 0.3s;
}

.curr {
    margin-top: 1.2em;
    margin-bottom: 1.2em;
}

.next {
    -moz-transition: margin-top 0.3s;
    -o-transition: margin-top 0.3s;
    -ms-transition: margin-top 0.3s;
    transition: margin-top 0.3s;
}

.next_clicked {
    /*margin-bottom: 0.9em;*/
    -moz-transition: margin-top 0.3s;
    -o-transition: margin-top 0.3s;
    -ms-transition: margin-top 0.3s;
    transition: margin-top 0.3s;
}

.load_more{
    text-align: center;
    margin-top: 1em;
}

.clickable {
    cursor: pointer;
}

#footer {
    text-align: center;
    font-size: 0.7em;
    color: #999;
    text-decoration: none;
}

#footer a {
    text-decoration: none;
    color: #999;
}

#footer a:hover {
    text-decoration: none;
    color: #666;
}

#footer a:visited {
    text-decoration: none;
    color: #999;
}

@media only screen and (max-width: 800px) {
    .query {
        font-size: 1.2em;
    }
    #search_container_default {
        width: 90%;
        height: 16em;
    }
}